.advantages-sedona {
  width: 320px;
}

.advantages-sedona__gist {
  margin: 0 auto;
  padding: 37px 27px 25px 27px;
  background-color: $background-color-auxiliary;
  color: $color-text-light;
}

.advantages-sedona__title {
  @include text;
  margin: 0;
  margin-bottom: 8px;
}

.advantages-sedona__services {
  @include text;
  color: $color-text-title;
  margin: 0;
}

.advantages-sedona__list {
  min-height: 410px;
  list-style: none;
  padding: 0;
  padding-top: 10px;
  margin: 0;
}

.advantages-sedona__item {
  width: 215px;
  padding-left: 70px;
  padding-top: 28px;
  text-align: left;
  padding-bottom: 0;
}

.advantages-sedona__text {
  margin-bottom: 0;
  margin-top: 13px;
}

.advantages-sedona__img {
  vertical-align: bottom;
}

.advantages-sedona--background {
  padding: 26px 41px 23px 41px;
  width: 238px;
  background-color: $background-color-extra;

  .advantages-sedona__title {
    color: $color-text-title;
    margin-bottom: 8px;
  }

  .advantages-sedona__text-background {
    margin-bottom: 6px;
    margin-top: 17px;
  }

  .advantages-sedona__description {
    margin-top: 15px;
    margin-bottom: 4px;
  }
}

