@media (min-width: $tablet-width) {
  .advantages-sedona__gist {
    width: 162px;
    padding-top: 48px;
    padding-left: 47px;
    padding-right: 47px;
    padding-bottom: 23px;
    margin: 0;
  }

  .advantages-sedona__bridge {
    order: 2;
  }

  .advantages-sedona {
    width: 768px;
    display: flex;
    flex-wrap: wrap;
  }

  .advantages-sedona__list {
    display: flex;
    min-height: 330px;
    margin: 0;
    padding: 0;
  }

  .advantages-sedona__item {
    width: 156px;
    text-align: center;
    background-position: center 60px;
    background-size: 75px 72px;
    padding: 162px 49px 39px 50px;
  }

  .advantages-sedona__item--souvenir {
    order: 3;
    width: 162px;
  }

  .advantages-sedona__text {
    margin-top: 20px;
  }

  .advantages-sedona__services {
    @include text(21px);
  }

  .advantages-sedona-wrapper {
    display: flex;
    width: 768px;
  }

  .advantages-sedona--background {
    width: 156px;
    padding: 46px 50px 36px 50px;
    margin: 0;
  }

  .advantages-sedona--background .advantages-sedona__title {
    margin-bottom: 10px;
  }

  .advantages-sedona--background .advantages-sedona__text-background {
    margin-top: 21px;
  }

  .advantages-sedona__title {
    @include text(21px);
    margin-bottom: 12px;
  }

  .advantages-sedona__number {
    padding-left: 49px;
    padding-right: 48px;
  }

  .advantages-sedona__description {
    margin-bottom: 0;
    margin-top: 19px;
  }

  .advantages-sedona__devil-bridge {
    order: 1;
    height: 256px;
  }

  .advantages-sedona__housing-image {
    height: 256px;
  }
}

@media (min-width: $desktop-width) {
  .advantages-sedona__gist {
    width: 265px;
    margin: 0;
    padding: 54px 67px 51px 68px;
  }

  .advantages-sedona {
    width: 1200px;
    display: flex;
    flex-wrap: wrap;
  }

  .advantages-sedona__list {
    display: flex;
    margin: 0;
  }

  .advantages-sedona__item {
    width: 300px;
    padding: 162px 50px 39px 50px;
  }

  .advantages-sedona-wrapper {
    display: flex;
    width: 1200px;
  }

  .advantages-sedona--background {
    width: 265px;
    padding: 47px 70px 56px;
    margin: 0;
  }

  .advantages-sedona__number {
    padding-left: 100px;
    padding-right: 100px;
  }

  .advantages-sedona__img {
    width: 800px;
    height: 256px;
  }

  .advantages-sedona__text--soouvenir {
    margin-left: 35px;
  }
}
